  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: url(./fonts/Montserrat-Black.ttf) format("truetype");
  unicode-range: U+0460-052F, U+1C80-1C88, U+20B4, U+2DE0-2DFF, U+A640-A69F, U+FE2E-FE2F;
}
