.header {
  font-size: 14px;
  padding-bottom: 50px;
}
@media (max-width: 1200px) {
  .header {
    font-size: 12px;
  }
}
@media (max-width: 1024px) {
  .header {
    font-size: 10px;
  }
}
@media (max-width: 750px) {
  .header {
    font-size: 8px;
  }
}
.header__nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 11;
  padding: 10px;
}
.header__nav::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 60%;
  background-color: #463f3f;
  box-shadow: 0px 20px 0px 6px #463f3f;
}
.header__container {
  padding: 0 20px;
  width: 100%;
  margin: auto;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 500px) {
  .header__container {
    flex-direction: column;
  }
}
.header__container-end {
  justify-content: space-between;
  justify-content: end;
}
.header__container-start {
  justify-content: space-between;
  justify-content: flex-start;
}
.header__flex {
  text-align: center;
  justify-content: flex-end;
  display: flex;
  font-size: 18px;
}
@media (max-width: 1500px) {
  .header__flex {
    font-size: 14px;
  }
}
@media (max-width: 1024px) {
  .header__flex {
    font-size: 12px;
  }
}
.header__item {
  position: relative;
  color: #fff;
  font-weight: bold;
  padding: 0.5rem 1.5rem;
  transition: all 0.5s;
  text-decoration: none;
  background-color: transparent;
  cursor: pointer;
}
@media (max-width: 1200px) {
  .header__item {
    font-size: 12px;
    padding: 4px 10px;
  }
}
@media (max-width: 1024px) {
  .header__item {
    font-size: 10px;
    padding: 2px 8px;
  }
}
@media (max-width: 750px) {
  .header__item {
    font-size: 8px;
  }
}
.header__item:hover {
  color: red;
}
.header__block-logo {
  display: flex;
  height: 40px;
  transform: skew(0deg);
}
@media (max-width: 1200px) {
  .header__block-logo {
    height: 30px;
  }
}

.nav__phones {
