  line-height: 1.5;
  border-radius: 0.25rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  cursor: pointer;
}

.btn:not(:disabled):not(.disabled) {
  cursor: pointer;
}

.raisingPage {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  background-image: url("../../assets/images/modal/arrow.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 15px 23px;
  z-index: 20;
  background-color: #064575;
  border: 2px solid #fff;
  border-radius: 50%;
}

.card__container {
  position: fixed;
  top: 40%;
  left: 0;
  right: 0;
  z-index: 11;
  padding: 10px;
  width: 30%;
}
@media (max-width: 800px) {
  .card__container {
    width: 50%;
  }
}
@media (max-width: 500px) {
  .card__container {
    width: 90%;
  }
}
.card__container::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: white;
}
.card__center {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: 0 15px;
}
.card__title {
  display: flex;
  justify-content: center;
  font-weight: bold;
  position: relative;
  color: black;
  z-index: 11;
}
.card__form {
  position: relative;
  width: 50%;
  padding: 15px;
  background-color: #bbbbbb;
}
.card__form-control {
  position: relative;
  display: inline-flex;
  width: 100%;
  margin-bottom: 8px;
  align-items: center;
  height: 40px;
  padding: 0 16px;
  border: 1px solid #E0E0E0;
}
.card__button {
  color: #34495e;
  position: relative;
  text-transform: uppercase;
}
.card__clouse {
  display: flex;
  justify-content: flex-end;
  cursor: pointer;
  color: black;
  position: relative;
  padding: 10px;
}

.container {
  font-size: 18px;
}
@media (max-width: 1024px) {
  .container {
    font-size: 16px;
  }
}
@media (max-width: 750px) {
  .container {
    font-size: 14px;
  }
}
@media (max-width: 500px) {
  .container {
    font-size: 10px;
  }
}

.bt_modal__detail {
  display: flex;
}
.bt_modal__detail-img {
  height: 0%;
  margin-right: 80px;
}
@media (max-width: 1024px) {
  .bt_modal__detail-img {
    display: none;
  }
}
.bt_modal__text {
  text-align: justify;
}

.stretched-link::after {
  position: relative;
}

.modal-body {
  padding: 5% 20%;
}
@media (max-width: 1024px) {
  .modal-body {
    padding: 2% 3%;
  }
}

.modal-dialog {
  max-width: 100%;
}

.row-cols-2 > * {
